body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.calculator {
  text-align: center;
  margin: 25px;
  display: inline-block;
  border: solid;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.centered {
  position: fixed;
  /* top: 50%; */
  left: 50%;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, 0%);
      -ms-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
}

.calculator .btn{
  height: 35px;
  width: 35px;
  padding: 0px;
}

.calculator .btn.equal-btn {
  width: 70px;
}

.calculator .btn.zero-button {
  width: 70px;
}

.buttons {
  overflow: hidden;
  display: inline-block;
}

.buttons .btn {
  border-color: black;
}

.numberButtons {
  float: left;
  margin-right: 5px;
}

.operatorButtons {
  float: left;
}

.result-bar {
  text-align: center;
  /* width: 100%; */
  margin: 5px;
  margin-bottom: 15px;
}

.result-bar input{
  text-align: right;
}



